body {
  background: url('./images/background.png');
  color: white;
  font-family: Franklin Gothic Medium , Arial Narrow , Arial , sans-serif;
  margin: 0%;
}

.header-h1 {
  color: white;
  font-size: 4rem;
  font-weight: 100;
  margin: auto;
  margin-bottom: 2.5rem;
  margin-top: 2.5rem;
  text-align: center;
  text-transform: uppercase;
}

.elipse {
  border: 1px solid white;
  border-radius: 50%;
  height: 100px;
  margin-left: 600px;
  position: absolute;
  transform: scaleX(1.7) skew(-40deg);
  transition: 1s;
  width: 200px;
}

.elipse:hover {
  box-shadow: 3px 6px 10px rgba(162, 161, 161, 0.602);
  height: 200px;
  width: 300px;
}

.second-title {
  color: white;
  font-size: 2rem;
  font-weight: 100;
  text-align: center;
  text-transform: uppercase;
}

.line {

  /* border: 1px solid white; */
  height: 0;
  margin-bottom: 350px;
  margin-top: 230px;
  width: 0;
}

.line:hover {
  margin-top: 90px;
}

.planets-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  position: absolute;
}

.planet-image {
  transition: 1.5s;
  width: 170px;
}

.planet-image:hover {
  width: 200px;
}

.planets-image {
  width: 50px;
}


.planet-name {
  text-align: center;
}


.mission-card {
  background-color: rgba(50, 49, 49, 0.354);
  border: 1px solid white;
  border-radius: 20px;
  padding: 20px;
  transition: 1s;
  width: 496px;
}

.mission-card:hover {
  background-color: rgba(0, 0, 0, 0.309);
  padding: 40px;
}

.mission-name {
  font-size: 25px;
}

.mission-info {
  border: 1px solid white;
  border-radius: 20px;
  display: flex;
  gap: 5px;
  justify-content: space-around;
  margin-left: 0%;
  width: 496px;
}

.missions-container {
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
  justify-content: center;
}

.missions {
  background-color: rgba(255, 255, 255, 0.11);
  border-radius: 30px;
  margin: auto;
  margin-bottom: 50px;

  /* margin-top: 20rem; */
  padding: 10px;
  width: 90%;
}

@media screen and ( max-width : 600px ) {

  .elipse {
    margin-left: 100px;
    width: 100px;
  }

  .planet-image {
    width: 140px;
  }

  .planet-image:hover {
    width: 170px;
  }

  .missions-container {
    display: flex;
    flex-direction: column;
    gap: 15px;
    justify-content: center;
    margin: auto;
  }

  .mission-info {
    width: 95%;
  }

  .mission-card {
    width: 90%;
  }

  .mission-card:hover {
    padding: 20px;
  }

  .line {
    height: 0;
    width: 0;
  }

  .missions {
    margin-top: 650px;
  }

}
